import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import city from '../components/City/City.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },{
    path: '/City',
    name: 'City',
    component:city
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
